import React, { type FC } from 'react';
import { FileText, Plus, ChevronLeft, Users, User, Building2, AlertCircle, type LucideIcon } from 'lucide-react';

// ===========================
// Types
// ===========================

type FileSource = 'self' | 'opponent' | 'court';
type FileStatus = 'ready' | 'processing' | 'pending';

interface CaseFile {
  id?: string;
  name?: string;
  filename?: string;
  source?: FileSource;
  status?: FileStatus;
  pages?: number;
  claims_count?: number;
}

interface SourceLabel {
  label: string;
  icon: LucideIcon;
  color: string;
}

interface FilesSummaryProps {
  files?: CaseFile[];
  onAddFiles?: () => void;
  onManageFiles?: () => void;
  compact?: boolean;
  className?: string;
}

// ===========================
// Constants
// ===========================

const SOURCE_LABELS: Record<FileSource, SourceLabel> = {
  self: { label: '××¡××›×™× ×©×œ× ×•', icon: User, color: 'emerald' },
  opponent: { label: '××¡××›×™ ×”×™×¨×™×‘', icon: Users, color: 'rose' },
  court: { label: '××¡××›×™ ×‘×™×ª ××©×¤×˜', icon: Building2, color: 'blue' }
};

// ===========================
// Component
// ===========================

const FilesSummary: FC<FilesSummaryProps> = ({
  files = [],
  onAddFiles,
  onManageFiles,
  compact = false,
  className = ''
}) => {
  // ×§×‘×¥ ×œ×¤×™ ××§×•×¨ (×©×œ× ×• / ×™×¨×™×‘ / ×‘×™×ª ××©×¤×˜)
  const groupedFiles: Record<FileSource, CaseFile[]> = {
    self: files.filter(f => f.source === 'self' || !f.source),
    opponent: files.filter(f => f.source === 'opponent'),
    court: files.filter(f => f.source === 'court')
  };

  if (compact) {
    return (
      <div className={`bg-white/5 rounded-xl p-4 border border-white/10 ${className}`}>
        <div className="flex items-center justify-between mb-3">
          <div className="flex items-center gap-2">
            <FileText className="text-blue-400" size={18} />
            <span className="text-white font-medium">
              {files.length} {files.length === 1 ? '×§×•×‘×¥' : '×§×‘×¦×™×'}
            </span>
          </div>
          <button
            onClick={onManageFiles}
            className="text-blue-400 hover:text-blue-300 text-sm flex items-center gap-1"
          >
            × ×™×”×•×œ
            <ChevronLeft size={14} />
          </button>
        </div>

        {files.length > 0 ? (
          <div className="space-y-1 max-h-32 overflow-y-auto">
            {files.slice(0, 5).map((file, idx) => (
              <div key={file.id || idx} className="flex items-center gap-2 text-sm">
                <span className={`w-2 h-2 rounded-full flex-shrink-0 ${
                  file.source === 'opponent' ? 'bg-rose-400' :
                  file.source === 'court' ? 'bg-blue-400' : 'bg-emerald-400'
                }`} />
                <span className="text-white/80 truncate">{file.filename || file.name}</span>
              </div>
            ))}
            {files.length > 5 && (
              <div className="text-blue-300 text-xs">
                +{files.length - 5} ×§×‘×¦×™× × ×•×¡×¤×™×
              </div>
            )}
          </div>
        ) : (
          <button
            onClick={onAddFiles}
            className="w-full py-3 border-2 border-dashed border-white/20 rounded-lg text-blue-300 hover:border-blue-400 hover:text-blue-400 transition-colors flex items-center justify-center gap-2"
          >
            <Plus size={16} />
            ×”×•×¡×£ ×§×‘×¦×™×
          </button>
        )}
      </div>
    );
  }

  return (
    <div className={`bg-white/5 backdrop-blur-xl rounded-2xl p-6 border border-white/10 ${className}`}>
      <div className="flex items-center justify-between mb-4">
        <div className="flex items-center gap-3">
          <FileText className="text-blue-400" size={24} />
          <div>
            <h3 className="text-lg font-bold text-white">××¡××›×™ ×”×ª×™×§</h3>
            <p className="text-blue-300 text-sm">
              {files.length} {files.length === 1 ? '×§×•×‘×¥' : '×§×‘×¦×™×'} ×‘×ª×™×§
            </p>
          </div>
        </div>

        <div className="flex gap-2">
          <button
            onClick={onAddFiles}
            className="flex items-center gap-2 px-3 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors text-sm font-medium"
          >
            <Plus size={16} />
            ×”×•×¡×£
          </button>
          {files.length > 0 && (
            <button
              onClick={onManageFiles}
              className="px-3 py-2 bg-white/10 text-white rounded-lg hover:bg-white/20 transition-colors text-sm font-medium"
            >
              × ×™×”×•×œ ××œ×
            </button>
          )}
        </div>
      </div>

      {/* ×‘×“×™×§×ª ××¡××›×™ ×™×¡×•×“ */}
      {files.length > 0 && (groupedFiles.self.length === 0 || groupedFiles.opponent.length === 0) && (
        <div className="mb-4 bg-amber-500/15 border border-amber-500/30 rounded-lg p-3">
          <div className="flex items-start gap-2">
            <AlertCircle className="text-amber-400 flex-shrink-0 mt-0.5" size={16} />
            <div className="text-sm">
              <p className="text-amber-200 font-medium">×˜×™×¤ ×œ×–×™×”×•×™ ×¡×ª×™×¨×•×ª</p>
              <p className="text-amber-100/70 text-xs">
                {groupedFiles.self.length === 0 && '×—×¡×¨×™× ××¡××›×™× ×©×œ× ×• â€¢ '}
                {groupedFiles.opponent.length === 0 && '×—×¡×¨×™× ××¡××›×™ ×”×™×¨×™×‘'}
              </p>
            </div>
          </div>
        </div>
      )}

      {files.length === 0 ? (
        <div className="text-center py-8">
          <FileText className="mx-auto mb-3 text-blue-400/50" size={48} />
          <p className="text-blue-300/70 mb-4">××™×Ÿ ×§×‘×¦×™× ×‘×ª×™×§</p>
          <button
            onClick={onAddFiles}
            className="px-6 py-3 bg-blue-600 text-white rounded-xl hover:bg-blue-700 transition-colors font-medium flex items-center gap-2 mx-auto"
          >
            <Plus size={20} />
            ×”×¢×œ×” ××ª ×”××¡××š ×”×¨××©×•×Ÿ
          </button>
        </div>
      ) : (
        <div className="space-y-4">
          {/* ×§×‘×•×¦×•×ª ×œ×¤×™ ××§×•×¨ */}
          {(Object.entries(groupedFiles) as [FileSource, CaseFile[]][]).map(([source, sourceFiles]) => {
            if (sourceFiles.length === 0) return null;
            const { label, icon: Icon, color } = SOURCE_LABELS[source];

            return (
              <div key={source}>
                <div className="flex items-center gap-2 mb-2">
                  <Icon size={16} className={`text-${color}-400`} />
                  <span className={`text-${color}-300 text-sm font-medium`}>
                    {label} ({sourceFiles.length})
                  </span>
                </div>
                <div className="grid gap-2">
                  {sourceFiles.map((file, idx) => (
                    <div
                      key={file.id || idx}
                      className={`flex items-center justify-between p-3 bg-white/5 rounded-lg border border-${color}-500/20 hover:border-${color}-500/40 transition-colors`}
                    >
                      <div className="flex items-center gap-3 flex-1 min-w-0">
                        <FileText className={`text-${color}-400 flex-shrink-0`} size={20} />
                        <div className="min-w-0">
                          <p className="text-white font-medium truncate">
                            {file.filename || file.name}
                          </p>
                          <div className="flex gap-3 text-xs text-blue-300/70">
                            {file.pages && <span>{file.pages} ×¢××•×“×™×</span>}
                            {(file.claims_count ?? 0) > 0 && <span>{file.claims_count} ×˜×¢× ×•×ª</span>}
                          </div>
                        </div>
                      </div>

                      <span className={`text-xs px-2 py-1 rounded-full ${
                        file.status === 'ready'
                          ? 'bg-emerald-500/20 text-emerald-300'
                          : file.status === 'processing'
                          ? 'bg-yellow-500/20 text-yellow-300'
                          : 'bg-white/10 text-white/60'
                      }`}>
                        {file.status === 'ready' ? '××•×›×Ÿ' :
                         file.status === 'processing' ? '×× ×ª×—...' : ''}
                      </span>
                    </div>
                  ))}
                </div>
              </div>
            );
          })}
        </div>
      )}
    </div>
  );
};

export default FilesSummary;
